.banner {
    /* background-image: url(https://images.pexels.com/photos/12709063/pexels-photo-12709063.png?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1); */
    background-image: url(../../images/bulb\(2\).jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    background-clip: content-box;
    background-blend-mode: multiply;
    width: 100%;
    height: 100vh;
}

.banner .contents {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    margin-left: 10%;
    justify-content: center;
    width: 75%;
    height: 100%;
}

.banner .contents>*:not(h1) {
    margin-bottom: 8px;
}

.contents>* {
    transform: perspective(200);
}

